.my-own-container {
  height: 124px;
  width: 320px;

  @media screen and (min-width: 768px) {
    height: 264px;
    width: 768px;
  }

  @media screen and (min-width: 1280px) {
    height: 430px;
    width: 1280px;
  }
}

.gallery {
  padding-bottom: 40px;

  background: linear-gradient(#fff5f6 44%, #fff5f6 39.9%, #fff 10%);

  @media screen and (min-width: 768px) {
    padding-bottom: 40px;
  }

  @media screen and (min-width: 1280px) {
    padding-bottom: 60px;
  }

  // <<<<<<< cool-gallery
  &--background-gradient {
    position: relative;
  }

  &--main-big-card {
    width: 81px;
    height: 81px;
    z-index: 1;

    @media screen and (min-width: 768px) {
      width: 175px;
      height: 175px;
    }

    @media screen and (min-width: 1280px) {
      width: 285px;
      height: 285px;
    }
  }

  &--little-size-card {
    width: 38px;
    height: 38px;
    z-index: 1;

    @media screen and (min-width: 768px) {
      width: 86px;
      height: 86px;
    }

    @media screen and (min-width: 1280px) {
      width: 139px;
      height: 139px;
    }
  }
}

.gallery__img-effects {
  transition: transform 650ms;
  border-radius: 14%;

  &:hover,
  &:focus {
    z-index: 1;
    transform: scale(1.2);
  }
}

.gallery--main-big-card:hover,
.gallery--main-big-card:focus {
  cursor: pointer;
  z-index: 2;
  transform: perspective(700px) rotateX(15deg);
}

.gallery--little-size-card:hover,
.gallery--little-size-card:focus {
  cursor: pointer;
  z-index: 2;
  transform: perspective(400px) rotateX(20deg);
}

